@use "global";

#boatbuilder-app main {
  position: relative;
  max-width: 1200px;
  min-height: 100vh;
  margin: 0 auto;
  color: black;
}

#boatbuilder-app .sidebar {
  position: fixed;
  top: 90px;
  left: 0;
  width: 50px;
  height: 100%;
  z-index: 101;
  background: #f7f7f7;

  @media screen and (max-width: 992px) {
    display: none;
  }
  a {
    color: black;
    font-size: 16px;
    font-weight: 400;
    padding: 12px;
    transition: all 0.3s;
    display: block;
    text-decoration: none;

    &.active {
      font-weight: bold;
      border-left: 4px solid var(--accent);
    }

    &:hover {
      color: var(--accent);
    }
  }
}

.admin-bar #boatbuilder-app .sidebar {
  top: 122px;
}

#boatbuilder-app .page {
  margin-left: 50px;
  padding: 40px;

  @media screen and (max-width: 700px) {
    margin-left: 0px;
    margin-bottom: 80px;
    padding: 10px;
  }
  header {
    text-align: center;
    padding: 20px;
    padding-top: 0px;
    @media screen and (max-width: 700px) {
      padding: 0px;
    }
  }
}

#boatbuilder-app footer {
  display: grid;
  grid-template-columns: max-content auto auto 35%;
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  padding: 10px 50px;
  z-index: 100;
  background: #f7f7f7;
  align-items: center;
  .disclaimer {
    margin: 0;
    font-size: 12px;
    line-height: 1.4;
    padding: 0px 20px;
  }
  @media screen and (max-width: 700px) {
    padding: 10px;
    grid-template-columns: 100%;
    .disclaimer {
      padding: 10px 0px;
    }
  }
  h2 {
    font-size: 25px;
    padding-right: 20px;
    margin: 0px;
  }
  h3 {
    font-size: 24px;
    margin: 0px;
    & > span {
      display: block;
      color: black;
      font-weight: bold;
      font-size: 14px;
    }

    &.total-msrp {
      font-weight: 400;
      font-size: 13px;
      color: #5d5d5d;
      line-height: 1;
      & > span {
        font-weight: 400;
        font-size: 12px;
        display: inline-block;
      }
    }
  }
  .nav-buttons {
    text-align: right;
    @media screen and (max-width: 700px) {
      text-align: center;
    }
    a {
      margin: 5px;
      text-decoration: none;
      display: inline-block;
      background-color: white;
      line-height: 1;
      text-transform: uppercase;
      font-weight: 700;
      padding: 15px 30px;
      border: 2px solid black;
      font-family: var(--headings-font);
      transition: all 0.3s;
      color: black;

      &:hover {
        background-color: var(--accent);
        border-color: var(--accent);
        color: white;
      }
    }
  }
}

#boatbuilder-app .part-list.layout-rows {
  margin-bottom: 30px;
}

#boatbuilder-app .part-list.layout-box {
  display: grid;
  grid-template-columns: 25% 25% 25% 25%;
  column-gap: 20px;
  row-gap: 20px;
  margin-bottom: 30px;

  @media screen and (max-width: 700px) {
    grid-template-columns: 50% 50%;
    column-gap: 10px;
  }
  .part-container {
    position: relative;
    font-size: 1.5rem;
    font-weight: 700;
    border: lightgray 1px solid;
    color: black;
    background: white;
    text-align: center;
    transition: all 0.3s;
    cursor: pointer;
    &.active,
    &:hover {
      border: var(--accent) 1px solid;
      background: var(--accent);
      color: white;

      .part-image-tooltip a {
        color: white;
      }
    }

    &.active {
      &::before {
        content: "";
        position: absolute;
        top: 30%;
        left: 50%;
        width: 25px;
        height: 50px;
        border-radius: 2px;
        border-right: 10px solid white;
        border-bottom: 10px solid white;
        transform: translateX(-50%) translateY(-50%) rotate(45deg);
      }
    }
    .box-bg-color {
      padding-top: 40%;
    }
    .part-image-tooltip {
      display: inline-block;
      vertical-align: middle;
      margin-left: 10px;
      a {
        display: block;
        padding: 10px;
        border-radius: 100%;
        line-height: 0.7;
        cursor: pointer;
        color: var(--accent);
        background-color: transparent;
        transition: all 0.3s;
        &:hover {
          transform: scale(1.1);
          background-color: rgba(255, 255, 255, 0.2);
        }
      }
    }
    @media screen and (max-width: 700px) {
      .part-image-tooltip a {
        padding: 15px;
        transform: scale(1.1);
        background-color: rgba(255, 255, 255, 0.2);
      }
    }
    .part-label {
      display: block;
      padding: 10px 0;
    }
    .part-label-text {
      display: inline-block;
      vertical-align: middle;
    }

    .part-price {
      display: none;
    }

    &.disabled {
      cursor: not-allowed;
      .part-checkbox {
        background: #d6d6d6;
        border-color: #d6d6d6;
      }
      .part-label,
      .part-price {
        color: #d6d6d6;
      }
    }
  }
}

#boatbuilder-app .part-list.layout-rows .part-container {
  display: grid;
  grid-template-columns: 30px auto max-content min-content;
  align-items: center;
  padding: 6px 0;
  border-bottom: lightgray 1px solid;
  font-size: 1rem;
  color: black;
  .part-label {
    vertical-align: middle;
    padding-left: 10px;
    font-size: 14px;
  }
  .part-image-tooltip {
    vertical-align: middle;
    margin-left: 5px;
    a {
      cursor: pointer;
      color: var(--accent);

      transition: all 0.3s;
      &:hover {
        color: #8e8e8e;
      }
    }
  }

  .part-quantity {
    .part-quantity-label {
      margin-right: 2px;
      font-family: var(--headings-font);
      font-weight: var(--headings-weight);
      text-transform: uppercase;
      font-size: 10px;
    }

    input {
      color: black;
      padding: 5px 10px;
      height: auto;
      border-width: 1px;
      border-style: solid;
      border-color: #eaeaea;
      border-radius: 0px;
      background: #fafafa;
      box-shadow: none;
      box-sizing: border-box;
      transition: all 0.2s linear;
      margin-right: 20px;
      width: max-content;
      display: inline-block;
    }
  }
  .part-price {
    font-weight: 700;
    text-align: right;
    font-size: 14px;
    .part-price-discount {
      display: block;
      line-height: 1;
    }

    .part-price-full {
      font-weight: 400;
      font-size: 10px;
      color: #5d5d5d;
      line-height: 1;
      display: block;
      padding-top: 5px;
    }
  }

  &:last-child {
    border: none;
  }

  .part-checkbox {
    display: inline-block;
    width: 30px;
    height: 30px;
    background: white;
    border-radius: 3px;
    margin: 2px;
    position: relative;
    transition: all 0.3s;
    border: 2px solid gray;
    vertical-align: middle;
    &:after {
      content: "";
      opacity: 0;
      position: absolute;
      border-left: 5px solid white;
      border-bottom: 5px solid white;
      width: 20px;
      height: 10px;
      transform: translateX(-50%) translateY(-50%) rotate(-45deg);
      top: 48%;
      left: 50%;
    }
  }

  .part-checkbox.active {
    background: var(--accent);
    border: 2px solid var(--accent);
    &:after {
      opacity: 1;
    }
  }
  &.disabled {
    cursor: not-allowed;
    .part-checkbox {
      background: #d6d6d6;
      border-color: #d6d6d6;
    }
    .part-label,
    .part-price {
      color: #d6d6d6;
    }
  }
}

#boatbuilder-app .image-lightbox {
  display: none;
  position: fixed;
  z-index: 102;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  padding: 50px;
  height: 100%;
  &.active {
    display: block;
  }
  .image-center {
    position: relative;
    top: 50%;
    left: 50%;
    transform: translateX(-50%) translateY(-50%);
    z-index: 10;
    max-width: 90vh;
    max-height: 90vh;
  }
  .image-container {
    position: relative;
    z-index: 10;
    overflow: hidden;
    img {
      max-width: 100%;
      height: auto;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translateX(-50%) translateY(-50%);
    }
  }
  .image-lightbox-bg {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background: rgba(0, 0, 0, 0.75);
  }
  .image-lightbox-close-btn {
    position: absolute;
    top: 0;
    right: 0;
    padding: 5px;
    background: white;
    height: 30px;
    width: 30px;
    z-index: 10;
    cursor: pointer;
    color: var(--accent);
    transition: all 0.3s;

    &:hover {
      color: #8e8e8e;
    }
  }
}

#boatbuilder-app .summary-page {
  header {
    width: 80%;
    margin: 0 auto;
    margin-bottom: 80px;
    padding: 0;

    @media screen and (max-width: 700px) {
      width: 100%;
    }
    .summary-header-container {
      display: grid;
      grid-template-columns: 50% 50%;

      @media screen and (max-width: 700px) {
        grid-template-columns: 100%;
      }
      .summary-image {
        padding: 20px;
      }

      .summary-total-price-container {
        padding: 20px;
        text-align: left;
      }
      .summary-total-price-label {
        display: block;
        color: #a3a3a3;
        font-size: 20px;
        text-transform: uppercase;
        line-height: 1;
      }

      .summary-total-price {
        display: block;
        line-height: 1;
        color: var(--accent);
        font-weight: bold;
        font-size: 40px;

        & > span {
          display: block;
          font-size: 16px;
          color: black;
          padding-bottom: 20px;
          text-transform: uppercase;
        }

        @media screen and (max-width: 700px) {
          font-size: 30px;
        }
      }
      .summary-full-price {
        display: block;
        line-height: 1;
        color: #5d5d5d;
        font-weight: 400;
        font-size: 20px;
        & > span {
          font-size: 16px;
          padding: 5px;
        }
        @media screen and (max-width: 700px) {
          font-size: 16px;
        }
      }
    }
  }
  .summary-details {
    text-align: center;
    margin-bottom: 80px;
    h3 {
      margin: 0;
    }
    p {
      color: black;
      font-size: 15px;
      margin-bottom: 26px;
    }
    .summary-part-details {
      display: grid;
      grid-template-columns: 33% 67%;
      text-align: left;
      padding: 20px 0;
      border-top: 1px solid rgb(224, 224, 224);

      @media screen and (max-width: 700px) {
        grid-template-columns: 100%;
      }
      .summary-group {
        font-weight: bold;
        font-size: 20px;
        text-transform: uppercase;
        color: black;
      }

      .summary-parts {
        margin: 0;
      }
      .summary-part {
        display: grid;
        grid-template-columns: auto max-content;
        align-items: center;

        .summary-part-label {
          display: list-item;
          color: black;
          font-size: 15px;
        }

        .summary-part-included {
          text-align: right;
          font-size: 11px;
          font-weight: bold;
          padding-left: 10px;
        }

        .summary-part-price {
          text-align: right;
          font-weight: bold;
          font-size: 18px;
          color: var(--accent);
          padding-left: 10px;
          .summary-part-price-discount {
            display: block;
            line-height: 1;
          }

          .summary-part-price-full {
            font-weight: 400;
            font-size: 10px;
            color: #5d5d5d;
            line-height: 1;
            display: block;
            padding-top: 5px;
          }
        }
      }
    }
  }
  .footer-heading {
    text-align: center;
  }
  .summary-btns {
    text-align: center;
    display: grid;
    grid-template-columns: 33% 33% 33%;
    @media screen and (max-width: 700px) {
      grid-template-columns: 100%;
    }
    a {
      margin: 0px;
      text-decoration: none;
      display: block;
      background-color: #fff;
      line-height: 1;
      text-transform: uppercase;
      font-weight: 700;
      padding: 20px 0px;
      border: 2px solid #000;
      font-family: var(--headings-font);
      transition: all 0.3s;
      color: #000;
      letter-spacing: 1px;
      width: 100%;
      @media screen and (max-width: 700px) {
        margin: 2px 0px;
      }

      &.summary-btn-center {
        border-left: 1px solid black;
        border-right: 1px solid black;
        @media screen and (max-width: 700px) {
          border-left: 2px solid black;
          border-right: 2px solid black;
        }
      }
      &.summary-btn-left {
        border-right: 1px solid black;
        @media screen and (max-width: 700px) {
          border-right: 2px solid black;
        }
      }

      &.summary-btn-right {
        border-left: 1px solid black;
        @media screen and (max-width: 700px) {
          border-left: 2px solid black;
        }
      }

      &:hover {
        background-color: var(--accent);
        border-color: var(--accent);
        color: white;
      }
    }
  }
}

#boatbuilder-app .form-popup .form-success {
  display: none;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 8;
  background: white;
  width: 100%;
  height: 100%;
  text-align: center;
  color: rgb(33, 199, 33);
  align-items: center;
  justify-content: center;
  flex-direction: row;
  flex-wrap: wrap;
  .icon-container {
    font-size: 48px;
    line-height: 1;
  }
  .message {
    color: rgb(33, 199, 33);
    font-family: var(--text-font);
    text-transform: none;
    font-size: 24px;
  }

  &.active {
    display: flex;
  }
}

#boatbuilder-app .form-popup .loader {
  display: none;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 9;
  background: rgba(0, 138, 244, 0.8);
  width: 100%;
  height: 100%;
  &:after {
    content: " ";
    position: relative;
    top: 50%;
    left: 50%;
    display: block;
    width: 64px;
    height: 64px;
    margin: 8px;
    border-radius: 50%;
    border: 6px solid #fff;
    border-color: #fff transparent #fff transparent;
    transform-origin: top left;
    animation: lds-dual-ring 1.2s linear infinite;
  }

  &.active {
    display: block;
  }
  @keyframes lds-dual-ring {
    0% {
      transform: rotate(0deg) translateX(-50%) translateY(-50%);
    }
    100% {
      transform: rotate(360deg) translateX(-50%) translateY(-50%);
    }
  }
}

#boatbuilder-app .formDealer,
#boatbuilder-app .formEmail {
  background: white;
  padding: 40px;
  @media screen and (max-width: 700px) {
    padding: 30px 0px 0px;
  }
  .form-error {
    background: #fb4848;
    padding: 5px 10px;
    color: white;
    font-weight: bold;
    font-size: 12px;
    text-align: center;
  }
  input[type="submit"] {
    display: block;
    background: var(--accent);
    color: white;
    border-radius: 0;
    border: none;
    text-transform: uppercase;
    font-size: 12px;
    font-weight: 700;
    font-family: var(--headings-font);
    width: 100%;
  }
  .form-container {
    display: grid;
    grid-template-columns: 50% 50%;

    .form-input-container {
      padding: 10px;

      label {
        font-size: 10px;
        text-transform: uppercase;
        font-weight: 700;
        display: block;
      }

      input,
      select {
        width: 100%;
        display: block;
        box-sizing: border-box;
        background: #f9f9f9;
        border: none;
        border-radius: 0;
        padding: 10px;
        color: black;
        font-size: 15px;
      }
    }
  }
}

#boatbuilder-app .formEmail .form-container {
  grid-template-columns: 100%;
}
#boatbuilder-app .form-popup {
  display: none;
  position: fixed;
  z-index: 102;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  padding: 50px;
  height: 100%;
  @media screen and (max-width: 700px) {
    padding: 0px;
  }
  &.active {
    display: block;
  }
  .form-popup-center {
    position: relative;
    top: 50%;
    left: 50%;
    transform: translateX(-50%) translateY(-50%);
    z-index: 10;
    max-width: 400px;
    max-height: 90vh;
  }
  .form-popup-container {
    position: relative;
    z-index: 10;
    overflow: hidden;
  }
  .form-popup-bg {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background: rgba(0, 0, 0, 0.75);
  }
  .form-popup-close-btn {
    position: absolute;
    top: 0;
    right: 0;
    padding: 5px;
    background: white;
    height: 30px;
    width: 30px;
    z-index: 10;
    cursor: pointer;
    color: var(--accent);
    transition: all 0.3s;

    &:hover {
      color: #8e8e8e;
    }
  }
}
